
/** Category Meta **/

.entry_cats {
	
	margin-bottom: 10px;
}

.entry_cats ul.post-categories {
	
	list-style: none;
	margin: 0px;
}

.entry_cats ul.post-categories li {
	
	display: inline-block;
	margin-right: 5px;
	margin-bottom: 5px;
}

.entry_cats ul.post-categories li a {
	
	display: inline-block;
    color: $white;
    font-size: 13px;
    background-color: $secondary_color;
    padding: 4px 10px;
    border-radius: 2px; 
    -webkit-border-radius: 2px; 
    -moz-border-radius: 2px;
    line-height: 1;
}

.entry_cats ul.post-categories li a:hover {
	
	color: $white;
    background-color: $primary_color;
}


/** Author, Date, and Comments Meta **/

.cm-post-meta {
	
	margin-top: 5px;
}

ul.post_meta {
	
	list-style: none;
	margin: 0px;
}

ul.post_meta li {
	
	display: inline-block;
	font-size: 14px;
	margin-right: 15px;
	margin-bottom: 0px;
}

ul.post_meta li a {
	
	color: $auth_date_cmnt_color;
	font-size: 14px;
	@include transation_delay();
}

ul.post_meta li a {
	
	align-items: center;
	display: flex;
	flex-wrap: nowrap;
}

ul.post_meta li.entry_cats {
	align-items: center;
	display: inline-flex;
	flex-wrap: wrap;
}

ul.post_meta li.entry_cats a {
	margin-left: 5px;
}

ul.post_meta li a:hover {
	
	color: $secondary_color;
	@include transation_delay();
}

ul.post_meta li span.cm-meta-icon {
	line-height: 1;
}

ul.post_meta li a span.cm-meta-icon {
	margin-right: 5px;
}	


ul.post_meta li span.cm-meta-icon svg {
	color: $auth_date_cmnt_color;
	fill: $auth_date_cmnt_color;
	height: 14px;
	width: 14px;
}



/** Tag Meta **/

.post_tags a {

    display:inline-block;
    font-size: 14px;
    background-color: $secondary_color;
    padding: 4px 15px;
    margin-right: 10px;
    margin-bottom: 10px;
    color: $white;
    text-transform: capitalize;
}

.post_tags a::before {

    content:'\0023';
    margin-right: 5px;
}

.post_tags a:hover {

    background-color: $primary_color;
    color: $white;
}